    /*********  PROMOCIONES *********/
    .call-outs {
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;
      flex-shrink: 1;
        justify-content: center;
        margin: 40px;
    }
    
    .call-out {
      position: relative;
      margin: .5em;
      width: 100%;
      max-width: 250px;
      text-align: center;	
        overflow: hidden;   
    }
    
    /* display .call-out as 1:1 ratio */
    .call-out:before {
      display: block;
      content: '';
      margin-top: 100%;
    }
    
    .links {
      background-color: black;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    
    .links a {	
      left: 0;
      right: 0;
      color: #FFF;
      padding: .5em 0;
    }
    
    .links a:visited,
    .links a:active,
    .links a:hover {
      color: #FFF;
    }
    
    @media screen and (min-width: 700px) {
      .call-out {
        width: 47%;
        margin: .5em 1em;
      }
    }
    
    @media screen and (min-width: 960px) {
      .call-outs {
        justify-content: space-between;
      }
      .call-out {
        width: 23.5%;
        margin: 0;
      }
      
    }
    
    .icons {
